<template>
    <form>
        <div>
            <input type="text" id="username" placeholder="ID"/>
        </div>
        <div>
            <input type="text" id="password" placeholder="PASSWORD"/><br/>
        </div>    
        <div>
            <input type="text" id="nickname" placeholder="Nickname"/><br/>
        </div>
        <button type="submit">SignUp</button>
    </form>

</template>

<script>
import { registUser } from '@/api/index.js';
import { validEmailAddr} from '@/utils/validation.js';

export default {
    methods: {
        submitForm() {
            try {
                const userData = {
                    username : this.username,
                    password : this.password,
                    nickname : this.nickname
                }
                const {data} = registUser(userData);
                console.log(data);
            } catch (error) {
                this.logMessage = error.response.data;        
            } finally {
                this.initForm();
            }
            
        },
        initForm() {
            this.username = '';
            this.password = '';
            this.nickname = '';
        }
    },
    computed: {
        isUsernameValid() {
            return validEmailAddr(this.username);
        }
    }
}
</script>